	--t: calc(var(--line) + var(--pt) - var(--ui-border-width));
	--b: calc(var(--line) - var(--pt) - var(--ui-border-width));
	--lr: calc(var(--line) / 1.618);
	padding: var(--t) var(--lr) var(--b) var(--lr);
	margin: 0;
}
